@font-face {
  font-family: "Muller";
  font-style: normal;
  font-weight: 400;
  src: local("Muller light"), url("../fonts/mullerlight-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "PlayfairDisplay";
  font-style: normal;
  font-weight: 700;
  src: local("PlayfairDisplay bold"), url("../fonts/playfair_display_bold-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  src: local("Raleway light"), url("../fonts/raleway-light-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  src: local("Raleway semibold"), url("../fonts/raleway-semibold-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  src: local("Raleway extrabold"), url("../fonts/raleway-extrabold-webfont.woff2") format("woff2");
}

body {
  margin: 0;
  font-family: "Raleway", "Muller", "PlayfairDisplay", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  color: #5e5e5e;
  background-color: #bbb;
  /* #fdfaf3 */
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
}

h2 {
  margin: 0;
}

.slider-button,
.button-arrow,
.play,
.menu {
  font-size: 0;
  cursor: pointer;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #7d7d7d;
  font-family: "Raleway";
  font-size: 18px;
  line-height: 30px;
}

.submit-orange {
  cursor: pointer;
}

.intro-text,
.phone-number {
  font-family: "Muller";
}

.intro-text {
  color: #ffffff;
  font-size: 14px;
  line-height: 20px;
}

.phone-number {
  color: #252525;
}

.logo {
  font-size: 0;
}

.navigation-list {
  display: none;
}

h1 {
  font-family: "PlayfairDisplay";
  font-size: 60px;
  color: #ffffff;
}

.button-hover,
.submit-orange {
  font-weight: 700;
  font-size: 15px;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
}

.service-column .button-hover {
  color: #00203a;
}

.headline {
  font-family: "PlayfairDisplay";
  color: #00203a;
  font-size: 45px;
}

.features-list .feature-item .feature-headline {
  font-family: "Raleway";
  font-weight: 500;
  font-size: 22px;
  color: #1a292f;
}

.current-ref,
.services-ref.current,
.service-column span {
  color: #00203a;
}

.service-headline {
  font-family: "PlayfairDisplay";
  font-weight: 400;
  font-size: 30px;
  color: #00203a;
}

.service-column span,
.service-text {
  font-size: 18px;
  line-height: 30px;
}

.video-text {
  font-family: "Raleway";
  font-size: 18px;
  color: #ffffff;
}

.steps-headline {
  font-weight: 500;
  font-size: 22px;
  line-height: 17px;
  color: #252525;
}

.steps-number {
  font-size: 12px;
}

.form-headline {
  font-family: "PlayfairDisplay";
  font-size: 45px;
  color: #ffffff;
}

.form-text,
.user-text,
.form-text2 {
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
}

.client-name {
  font-weight: 500;
  font-size: 22px;
  line-height: 30px;
  color: #252525;
}

.feedbacks-name {
  font-weight: 700;
  color: #efb172;
}

.sgn1,
.sgn2 {
  color: #ffc487;
}
